exports.push([module.id, "\n.project .titleBar {\n  padding: 0.5em;\n  font-size: 0.7em;\n}\n.project h2 {\n  font-family: \"Poiret One\";\n  margin-bottom: 5px;\n  font-size: 2em;\n}\n.project a {\n  text-decoration: none;\n}\n", "", {"version":3,"sources":["/Users/dapper/Dev/colinmcneil.github.io/src/components/src/components/Projects.vue"],"names":[],"mappings":";AAkKA;EACA,eAAA;EACA,iBAAA;CACA;AACA;EACA,0BAAA;EACA,mBAAA;EACA,eAAA;CACA;AACA;EACA,sBAAA;CACA","file":"Projects.vue?vue&type=style&index=1&lang=css&","sourcesContent":["<template>\n  <div>\n    <div id=\"projects\">\n      <div v-show=\"projects==[]\">Loading...</div>\n      <v-min-tile-grid>\n        <v-min-tile class= \"project\" v-for=\"project in projects\" \n        v-bind:key=\"project.name\" :title=\"project.name\">\n          <h4 class=\"desc\">{{project.description}}</h4>\n          <div class=\"meta\" :style=\"{\n            borderColor:$store.state.theme.secondaryColor,\n            color: $store.state.theme.secondaryColor}\">\n            <div>{{project.size+\" \"}} lines</div>\n            <div><a :href=\"project.displayURL\" target=\"_blank\" rel=\"noopener\">URL</a></div>\n            <div class=\"icon\" v-if=\"project.hacktoberfest\" title=\"Hacktoberfest PR\"></div>\n            <div class=\"icon\" v-else-if=\"project.fork\" title=\"Open Source Fork\"></div>\n          </div>\n        </v-min-tile>\n      </v-min-tile-grid>\n    </div>\n    <router-link id=\"back\" to=\"/menu\">back</router-link>\n  </div>\n</template>\n\n<script>\nimport { VMinTileGrid, VMinTile } from \"mina-vue\";\nrequire(\"es6-promise\").polyfill();\nrequire(\"isomorphic-fetch\");\n\nconst byDate = (a, b) => {\n  let date1 = Date.parse(a.updated_at);\n  let date2 = Date.parse(b.updated_at);\n  return date2 - date1;\n};\nconst mySquaredLabsRepos = [\n  \"squaredlabs.uconn.edu\",\n  \"illustration-webform\",\n  \"tech-workorder-form\"\n];\nconst myHacktoberfestRepos = [\n  'weatherapp',\n  'helectron',\n  'sqleton',\n  'Minimal-Notes'\n]\nexport default {\n  name: \"Projects\",\n  async mounted() {\n    const myReposURL = \"https://api.github.com/users/colinmcneil/repos\";\n    const squaredLabsReposURL = \"https://api.github.com/orgs/squaredlabs/repos\";\n    let myReposRaw = await fetch(myReposURL);\n    let myRepos = await myReposRaw.json();\n\n    let squaredLabsReposRaw = await fetch(squaredLabsReposURL);\n    let squaredLabsRepos = await squaredLabsReposRaw.json();\n    squaredLabsRepos = squaredLabsRepos.filter(current => {\n      return mySquaredLabsRepos.includes(current.name);\n    });\n\n    this.projects = myRepos\n      .concat(squaredLabsRepos)\n      .sort(byDate)\n      .map(project => {\n        project.displayURL = project.url.replace(\n          \"api.github.com/repos\",\n          \"github.com\"\n        );\n        if(myHacktoberfestRepos.includes(project.name))\n        project.hacktoberfest=true\n        return project;\n    });\n  },\n  data() {\n    return {\n      projects: [],\n      hovered: false\n    };\n  },\n  methods: {\n    projectHover(event) {\n      setTimeout(() => {\n        this.hovered = true;\n      }, 300);\n    },\n    projectLeave() {\n      this.hovered = false;\n    },\n    projectClick(url) {\n      if (this.hovered) {\n        window.open(url, \"_blank\");\n      }\n    }\n  },\n  components: {\n    VMinTileGrid,\n    VMinTile\n  }\n};\n</script>\n\n<style scoped>\n@import url(\"https://fonts.googleapis.com/css?family=Poiret+One\");\n@import url('https://fonts.googleapis.com/css?family=Inconsolata');\n#projects {\n  font-family: \"Inconsolata\";\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  height: 70%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n  overflow-y: auto;\n  padding: 1em;\n}\n.project {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  margin-bottom: 10%;\n  min-width: 300px;\n}\n.desc {\n  padding: 0.2em;\n}\n.meta {\n  display: flex;\n  justify-content: space-around;\n}\n.project .imgContainer {\n  width: 50%;\n  transform: translateX(50%);\n  cursor: pointer;\n}\n#back {\n  position: absolute;\n  bottom: 10%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-decoration: none;\n  color: black;\n  font-family: \"Poiret One\";\n  font-size: 2em;\n  transition: ease-in-out 0.3s all;\n  background-color: white;\n  padding: 5px;\n}\n#back:hover {\n  cursor: pointer;\n  opacity: 1;\n  padding: 10px;\n}\n.icon {\n  cursor: default;\n}\n@media only screen and (max-width: 500px) {\n  #projects {\n    width: 100%;\n    height: 300px;\n    transform: translate(-50%, -70%);\n  }\n}\n</style>\n<style>\n.project .titleBar {\n  padding: 0.5em;\n  font-size: 0.7em;\n}\n.project h2 {\n  font-family: \"Poiret One\";\n  margin-bottom: 5px;\n  font-size: 2em;\n}\n.project a {\n  text-decoration: none;\n}\n</style>\n"],"sourceRoot":""}]);

